<p>Estudiante de Diseo de Medios Interactivos con inters en trabajar en una industria competitiva, que est preocupada en la creatividad, el conocimiento y la innovacin, ms especficamente en las reas de Desarrollo Front-end, Desarrollo de Productos digitales con metodologas giles y Ux, como tambin Diseo de Experiencias Interactivas.</p>
